@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,800,700,300);
@import url(https://fonts.googleapis.com/css?family=Squada+One);
body {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  text-align: center;
  background: #fff; }

a {
  text-decoration: none;
  color: inherit; }

.active {
  background: darkred;
  color: #fff; }

.pizza {
  position: relative;
  width: 70%;
  border-radius: 10%;
  top: 0;
  left: 0; }

.welcome {
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  width: 35%;
  z-index: 1;
  border-radius: 5%; }

.none {
  display: none; }

body {
  padding: 20px 80px; }

header {
  background: #ccc;
  padding: 10px; }

#menu {
  height: 42px;
  list-style: none;
  margin: 10px 0 0 0;
  padding: 25px 10px;
  border-top: 4px double #AAA;
  border-bottom: 4px double #AAA;
  position: relative;
  text-align: center; }

#menu li {
  display: inline-block;
  width: 173px;
  margin: 0 10px;
  position: relative;
  transform: skewy(-3deg);
  backface-visibility: hidden;
  transition: 200ms all; }

#menu li a {
  text-transform: uppercase;
  font-family: 'Squada One', cursive;
  font-weight: 500;
  display: block;
  padding: 2px 10px;
  font-size: 35px;
  text-align: center;
  text-decoration: none;
  position: relative;
  z-index: 1;
  text-shadow: 1px 1px 0px #FFF, 2px 2px 0px #999, 3px 3px 0px #FFF;
  background-size: 4px 4px;
  box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.4) inset, 0 0 20px -5px rgba(0, 0, 0, 0.4), 0 0 0px 3px #FFF inset; }

#menu li:hover {
  width: 203px;
  margin: 0 -5px; }

#menu a:hover {
  color: #d90075; }

#menu li:after,
#menu li:before {
  content: '';
  position: absolute;
  width: 50px;
  height: 100%;
  background: lightcoral;
  transform: skewY(8deg);
  border-radius: 4px; }

#menu li:after {
  background-image: linear-gradient(left, transparent 0%, rgba(0, 0, 0, 0.4) 100%);
  right: 0;
  top: -4px; }

#menu li:before {
  left: 0;
  bottom: -4px;
  background-image: linear-gradient(right, transparent 0%, rgba(0, 0, 0, 0.4) 100%); }

.pizza-page .pizza-menu {
  display: flex;
  flex-wrap: wrap;
  max-width: 1200px;
  margin: auto;
  justify-content: space-between; }

.pizza-page .pizza {
  margin-bottom: 40px;
  padding-bottom: 20px;
  width: 30%;
  background: #eee;
  position: relative; }
  .pizza-page .pizza:hover {
    background-color: indianred; }
  .pizza-page .pizza h3 {
    font-family: 'Architects Daughter', cursive; }

.pizza-page p {
  font-size: 22px;
  user-select: none;
  cursor: pointer; }

.pizza-page .pizzaBtn {
  background: #3498db;
  width: 180px;
  padding: 4px 0;
  position: absolute;
  left: 50%;
  bottom: -5%;
  transform: translateX(-50%) translateY(-50%);
  border-radius: 12px; }
  .pizza-page .pizzaBtn:hover {
    color: #d90075; }

.drinkGnrl {
  margin-top: 60px;
  display: flex;
  flex-direction: row;
  justify-content: space-between; }

.drink {
  margin-bottom: 40px;
  padding-bottom: 20px;
  background: #eee;
  width: 20%;
  border-radius: 12px;
  position: relative; }
  .drink:hover {
    background-color: indianred; }
  .drink h3 {
    font-family: 'Architects Daughter', cursive; }

.drinkBtn {
  background: #3498db;
  width: 180px;
  padding: 4px 0;
  position: absolute;
  left: 50%;
  bottom: -10%;
  transform: translateX(-50%) translateY(-50%);
  border-radius: 12px; }
  .drinkBtn:hover {
    color: #d90075; }

.deleteAll {
  color: brown;
  margin-top: 45px; }

.delete {
  margin-left: 50px;
  color: red;
  font-size: 30px;
  user-select: none;
  cursor: pointer; }

table {
  margin: auto; }
  table tr {
    display: flex;
    flex-direction: column;
    border: 1px solid indianred; }
  table td {
    border: 1px solid black;
    margin: 10px; }

.sum {
  font-size: 30px; }

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); }

.popup {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  position: fixed;
  align-items: center;
  top: -50%;
  left: 50%;
  width: 400px;
  border-radius: 5px;
  height: 200px;
  transition: .6s;
  background: #fff;
  transform: translate(-50%, -50%); }

.popup h3 {
  text-align: center;
  font-size: 22px;
  width: 100%; }

.no, .yes {
  font-size: 20px;
  padding: 5px 10px;
  margin: 20px;
  cursor: pointer;
  user-select: none; }

.yes {
  color: green;
  border: 1px solid green; }

.no {
  color: red;
  border: 1px solid red; }

.clear {
  margin-left: 10%;
  border: 1px solid red;
  color: red;
  font-size: 16px;
  cursor: pointer;
  padding: 5px 10px;
  display: inline-block; }



